`<template>
  <div id="header">
    <div class="user-menu">
      <div class="user-pic">
        <img v-if="user.photo !== 'https://server.gold-ticket.uz/storage/app/'" :src="user.name" :alt="user.name">
        <img v-else src="https://gold-ticket.uz/tgs/avatar.png" :alt="user.name">
      </div>
      <div class="user-name">{{ this.user.name }}</div>
    </div>
    <div class="user-tools">
      <router-link tag="div" to="/settings" class="btn-icon settings">
        <ion-icon name="cog-outline" class="ai-icon"></ion-icon>
      </router-link>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "HeaderUserBar",
  data: () => ({
    user: null,
  }),
  mounted() {
    axios.get('https://server.gold-ticket.uz/api/user/' + Telegram.WebApp.initDataUnsafe.user.id)
        .then(response => {
          this.user = response.data.data
        })
        .catch(error => console.log(error));
  }
}
</script>

<style scoped>

</style>
`
