<template>
  <div id="bottom-navigation">
    <router-link tag="div" :to="item.className === 'home' ? '/' : item.className" class="btn" :class="item.className && active === item.className ? 'active' : ''" v-for="item in nav" :key="item.name">
      <ion-icon :name="item.name" class="btn-icon"></ion-icon>
      <div class="btn-text">{{ item.title }}</div>
    </router-link>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "BottomNavigationMenu",
  props: {
    active: String
  },
  data: () => ({
    user: null,
    nav: null
  }),
  async mounted() {
    await axios.get('https://server.gold-ticket.uz/api/user/' + Telegram.WebApp.initDataUnsafe.user.id)
        .then(response => {
          this.user = response.data.data
        })
        .catch(error => console.log(error));

    if (this.user.lang === 'ru') {
      this.nav = [
        {
          className: 'home',
          name: 'home',
          title: 'Главное'
        },
        {
          className: 'market',
          name: 'bag-handle',
          title: 'Маркет'
        },
        {
          className: 'partners',
          name: 'star-half-outline',
          title: 'Спонсоры'
        },
        {
          className: 'profile',
          name: 'person',
          title: 'Профиль'
        },
      ]
    } else {
      this.nav = [
        {
          className: 'home',
          name: 'home',
          title: 'Asosiy'
        },
        {
          className: 'market',
          name: 'bag-handle',
          title: 'Market'
        },
        {
          className: 'partners',
          name: 'star-half-outline',
          title: 'Homiylar'
        },
        {
          className: 'profile',
          name: 'person',
          title: 'Profil'
        },
      ]
    }
  }
}
</script>

