<template>
  <div id="container">
    <div id="alert" class="error" :class="premium ? '' : 'active'">
      <ion-icon name="warning" class="icon"></ion-icon>
      <div class="title" v-if="lang === 'ru'">Вы не оформили подписку!</div>
      <div class="title" v-else>Obunani sotib olmagansiz!</div>
      <div class="pay-btn" @click="subscripInvoice()">Оформить</div>
    </div>
    <HeaderUserBar />

    <div id="banner">
      <div class="banner-collage swiper">
        <div class="swiper-wrapper">
          <div class="swiper-slide">
            <div class="item-gradient"></div>
            <img src="../files/slider_1_ru.svg" alt="" v-if="lang === 'ru'"/>
            <img src="../files/slider_1_uz.svg" alt="" v-else/>
          </div>
          <div class="swiper-slide">
            <img src="../files/slider_2_ru.svg" alt="" v-if="lang === 'ru'"/>
            <img src="../files/slider_2_uz.svg" alt="" v-else/>
            <div class="item-gradient"></div>
          </div>
        </div>
      </div>
    </div>
    <div id="service">
      <div class="service-title" v-if="lang === 'ru'">Другие сервисы</div>
      <div class="service-title" v-else>Boshqa servislar</div>
      <div class="swiper service-cards">
        <div class="swiper-wrapper">
          <router-link tag="div" to="/qr-code-cashback" class="service-card swiper-slide">
            <div class="service-pic">
              <img src="../files/cashback.svg" alt="Service Partners">
            </div>
            <div class="service-title" v-if="lang === 'ru'">Кешбэк</div>
            <div class="service-title" v-else>Keshbek</div>
          </router-link>
          <a href="https://prizes.gamee.com/game-bot/karatekid2" class="service-card swiper-slide">
            <div class="service-pic">
              <img src="../files/game.svg" alt="Service Partners">
            </div>
            <div class="service-title">Ticket Game</div>
          </a>
          <router-link tag="div" to="/participation" class="service-card swiper-slide">
            <div class="service-pic">
              <img src="../files/buy_tickets.svg" alt="Service Partners">
            </div>
            <div class="service-title" v-if="lang === 'ru'">Учавствовать</div>
            <div class="service-title" v-else>Qatnashish</div>
          </router-link>
          <router-link tag="div" to="/give-a-dream" class="service-card swiper-slide">
            <div class="service-pic">
              <img src="../files/give_a_dream.svg" alt="Give a dream">
            </div>
            <div class="service-title" v-if="lang === 'ru'">Подари мечту</div>
            <div class="service-title" v-else>Orzu ulash</div>
          </router-link>
          <router-link tag="div" style="visibility: hidden;opacity: 0" to="/give-a-dream" class="service-card swiper-slide"></router-link>
        </div>
      </div>
    </div>

    <BottomNavigationMenu active="home" />
  </div>
</template>

<script>
import axios from 'axios'
import BottomNavigationMenu from "@/components/BottomNavigationMenu";
import HeaderUserBar from "@/components/HeaderUserBar";

export default {
  name: "Home",
  data: () => ({
    agreed: null,
    lang: null,
    premium: null
  }),
  components: {HeaderUserBar, BottomNavigationMenu},
  async mounted() {
    new Swiper(".banner-collage", {
      effect: "cards",
      grabCursor: true,
      autoplay: {
        delay: 2500,
      },
      slidesPerView: 'auto',
      // loop: true,
    });

    new Swiper(".service-cards", {
      watchSlidesProgress: true,
      slidesPerView: 3,
    })

    Telegram.WebApp.MainButton.hideProgress();
    Telegram.WebApp.MainButton.hide();
    Telegram.WebApp.BackButton.hide();

    await axios.get('https://server.gold-ticket.uz/api/user/' + Telegram.WebApp.initDataUnsafe.user.id)
        .then(response => {
          this.agreed = response.data.data.agreed;
          this.lang = response.data.data.lang;
          this.premium = response.data.data.subscription;
        })
        .catch(error => console.log(error));

    if (!this.user.agreed) {
      this.$router.push('/terms')
    }
  },
  methods: {
    async subscripInvoice()  {
      await axios.post('https://server.gold-ticket.uz/api/invoice/' + Telegram.WebApp.initDataUnsafe.user.id, {
        'lang': this.lang
      }).then(response => {
        if (this.lang === 'ru') {
          Telegram.WebApp.showPopup({
            'title': 'Успешно',
            'message': 'Бот вам отпрвил счёт для оплаты',
            'buttons': [
              {
                'id': 'success',
                'type': 'ok',
                'text': "Продолжить"
              }
            ]
          })
        } else {
          Telegram.WebApp.showPopup({
            'title': 'Muvofaqiyatli',
            'message': 'Bot sizga tolov uchun hisob yubordi',
            'buttons': [
              {
                'id': 'success',
                'type': 'ok',
                'text': "Davom etish"
              }
            ]
          })
        }
      }).catch(error => {
        if (!error.data.ok) {
          if (this.lang === 'ru') {
            Telegram.WebApp.showPopup({
              'title': 'Ошибка!',
              'message': 'Произошла ошибка, повторите попытку больше ',
              'buttons': [
                {
                  'id': 'success',
                  'type': 'ok',
                  'text': "Повторить"
                }
              ]
            })
          } else {
            Telegram.WebApp.showPopup({
              'title': 'Xato!',
              'message': 'Tolo hisibi yaratishda hatolik yuz berdi!\n\n Keyinroq qayta urunib koring',
              'buttons': [
                {
                  'id': 'success',
                  'type': 'ok',
                  'text': "Davom etish"
                }
              ]
            })
          }
        }
      });
    },
  },
}
</script>
